/** подготовка графики - ретина **/
html {
  box-sizing: border-box;
  height: 100%; }

*,
*::before,
*::after {
  box-sizing: inherit; }

body {
  position: relative;
  display: flex;
  flex-direction: column;
  font-family: "OpenSans", "Arial", sans-serif;
  font-style: normal;
  font-weight: normal; }

.visually-hidden {
  position: absolute;
  overflow: hidden;
  clip: rect(0 0 0 0);
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0; }

@keyframes scale {
  from {
    transform: scale(1); }
  to {
    transform: scale(1.1); } }

@keyframes hover-color-from-red {
  from {
    background-color: #e74c3c; }
  to {
    background-color: #fcd12a; } }

@keyframes hover-color-from-translate {
  from {
    background-color: translate; }
  to {
    background-color: #fcd12a; } }

@keyframes hover-menu {
  from {
    color: #ffffff;
    border-bottom: 1px solid transparent;
    padding-bottom: 10px; }
  to {
    color: #e74c3c;
    border-bottom: 1px solid #e74c3c;
    padding-bottom: 20px; } }

@keyframes hover-about {
  from {
    border: 1px solid #cbc6c0;
    transform: rotate(0); }
  to {
    border: 1px solid #e74c3c;
    transform: rotate(45deg); } }

@keyframes hover-color-about {
  from {
    color: #6a6a6a; }
  to {
    color: #e74c3c; } }

