.promo-slider {
  position: relative;
  background-image: url("../img/shadow-background.svg"), url("../img/slider-background.jpg");
  background-position: 0 0, 0 0;
  background-repeat: no-repeat, no-repeat;
  background-size: cover, cover; }
  .promo-slider::before {
    content: "";
    position: absolute;
    width: 50%;
    height: 130px;
    bottom: 0;
    left: 0;
    z-index: 2;
    background-color: #ffffff;
    clip-path: polygon(0 0, 0 130px, 100% 130px, 0 0); }
  .promo-slider::after {
    content: "";
    position: absolute;
    width: 50%;
    height: 130px;
    bottom: 0;
    right: 0;
    z-index: 2;
    background-color: #ffffff;
    clip-path: polygon(0 0, 0 130px, 100% 0, 0 0);
    transform: rotate(180deg); }

.promo-slider__container {
  position: relative;
  z-index: 3;
  padding: 325px 190px 500px; }
  .promo-slider__container::before {
    content: "";
    position: absolute;
    width: 74px;
    height: 74px;
    bottom: -37px;
    left: 50%;
    transform: translateX(-50%);
    z-index: 5;
    border-radius: 50%;
    background-color: rgba(244, 245, 249, 0.251); }
  .promo-slider__container::after {
    content: "";
    position: absolute;
    width: 64px;
    height: 64px;
    bottom: -32px;
    left: 50%;
    transform: translateX(-50%);
