  width: 100%;
  height: 100%; }

.promo-slider__slogan {
  margin-bottom: 50px;
  font-family: "SourceSansPro", "Arial", sans-serif;
  color: #ffffff;
  font-size: 24px;
  text-align: center; }

.promo-slider__title {
  position: relative;
  margin-bottom: 45px;
  padding-bottom: 45px;
  color: #ffffff;
  font-size: 72px;
  text-align: center;
