.promo-slider__link {
  display: block;
  padding: 18px 0;
  width: 225px;
  font-size: 14px;
  color: #ffffff;
  font-weight: bold;
  text-transform: uppercase;
  text-decoration: none;
  text-align: center;
  border-radius: 3px;
