    border: none; }
  .promo-slider__link:hover, .promo-slider__link:focus {
    background-color: #fcd12a;
    border: none;
    transform: scale(1.1);
    animation-name: scale, hover-color-from-translate;
    animation-duration: 500ms;
    animation-timing-function: ease-in-out; }
  .promo-slider__link--background:hover, .promo-slider__link--background:focus {
    animation-name: hover-color-from-red; }
  .promo-slider__link:active {
    background-color: #fcd12a;
    opacity: 0.5; }

.promo-slider__button,
.swiper-button-prev,
.swiper-button-next {
  position: absolute;
  top: 160px;
  left: 0;
  width: 45px;
  height: 45px;
  z-index: 5;
  padding: 10px 15px;
  border: none;
  border-radius: 2px;
  background-color: #e74c3c;
  cursor: pointer; }
  .promo-slider__button::after,
  .swiper-button-prev::after,
  .swiper-button-next::after {
    display: none; }
  .promo-slider__button--next,
  .swiper-button-prev--next,
  .swiper-button-next--next {
    left: unset;
    right: 0; }
  .promo-slider__button--next .promo-slider__icon,
  .swiper-button-prev--next .promo-slider__icon,
  .swiper-button-next--next .promo-slider__icon {
    transform: rotate(180deg); }
  .promo-slider__button:hover, .promo-slider__button:focus,
  .swiper-button-prev:hover,
  .swiper-button-prev:focus,
  .swiper-button-next:hover,
  .swiper-button-next:focus {
    background-color: #fcd12a;
    transform: scale(1.1);
    animation-name: scale, hover-color-from-red;
    animation-duration: 500ms;
    animation-timing-function: ease-in-out; }
  .promo-slider__button:disabled:hover, .promo-slider__button:disabled:focus,
  .swiper-button-prev:disabled:hover,
  .swiper-button-prev:disabled:focus,
  .swiper-button-next:disabled:hover,
  .swiper-button-next:disabled:focus {
    animation-name: none;
    background-color: #ffffff;
    transform: scale(1);
    cursor: default; }
  .promo-slider__button:active,
  .swiper-button-prev:active,
  .swiper-button-next:active {
    background-color: #fcd12a;
    opacity: 0.5; }
  @media (max-width: 1830px) {
    .promo-slider__button,
    .swiper-button-prev,
    .swiper-button-next {
      left: 0; }
      .promo-slider__button--next,
      .swiper-button-prev--next,
      .swiper-button-next--next {
        left: unset;
        right: 0; } }

.promo-slider__icon {
